function arrayToTreeGroup(data: any, pId: any) {
  let tree = [];
  let temp;
  for (let i = 0; i < data.length; i++) {
    if (data[i].pId == pId) {
      let obj = data[i];
      temp = arrayToTreeGroup(data, data[i].id);
      if (temp.length > 0) {
        obj.subs = temp;
      }
      tree.push(obj);
    }
  }
  return tree;
}
